import { useEffect, useState } from 'react';
import { CenterApi, CenterSaveApi } from 'utils/adminApi';
import { Button, Upload, message, Form } from 'antd';
import { PlusOutlined } from '@ant-design/icons';

const Center = () => {
  const [imageUrl, setImageUrl] = useState(null);
  const [avatarUrl, setAvatarUrl] = useState(null);

	useEffect(() => {
		CenterApi().then((res) => {
			setImageUrl(res.data.imageUrl)
			setAvatarUrl(res.data.avatarUrl)
		})
		}, [])

  const onChange = (info, type) => {
    if (info.file.status === 'done') {
      message.success('文件上传成功');
			if(type === 'img'){
      setImageUrl(info.file.response);
			}else if(type === 'avatar'){
				setAvatarUrl(info.file.response);
			}
    } else if (info.file.status === 'error') {
      message.error('文件上传失败');
    }
  };

  const onFinish = () => {
    CenterSaveApi({ imageUrl, avatarUrl }).then((res) => {
      message.success('保存成功');
    });
  };
  return (
    <div>
      <Form
        labelCol={{ span: 4 }}
        name="control-hooks"
        onFinish={onFinish}
      >
        <Form.Item label="个人中心横幅">
          <Upload
            className="flex justify-center items-center"
            name="file"
            action={process.env.REACT_APP_UPLOAD_SERVER}
            headers={
              localStorage.getItem('__web_token')
                ? { Authorization: `Bearer ${localStorage.__web_token}` }
                : null
            }
            showUploadList={false}
            onChange={(info) => onChange(info, 'img')}
          >
            {imageUrl ? (
              <img
                src={process.env.REACT_APP_SERVER + imageUrl}
                alt="avatar"
                className=" w-72 h-24 bg-red-400"
              />
            ) : (
              <div className="flex justify-center items-center w-72 h-24 bg-red-400">
                <PlusOutlined />
              </div>
            )}
          </Upload>
        </Form.Item>
        <Form.Item label="用户头像">
          <Upload
            className="flex justify-center items-center"
            name="file"
            action={process.env.REACT_APP_UPLOAD_SERVER}
            headers={
              localStorage.getItem('__web_token')
                ? { Authorization: `Bearer ${localStorage.__web_token}` }
                : null
            }
            showUploadList={false}
            onChange={(info) => onChange(info, 'avatar')}
          >
            {avatarUrl ? (
              <img
                src={process.env.REACT_APP_SERVER + avatarUrl}
                alt="avatar"
                className=" w-24 h-24 bg-red-400"
              />
            ) : (
              <div className="flex justify-center items-center w-24 h-24 bg-red-400">
                <PlusOutlined />
              </div>
            )}
          </Upload>
        </Form.Item>
        <Form.Item wrapperCol={{ span: 4, offset: 4 }}>
          <Button type="link" htmlType="submit">
            保存
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default Center;
